import { extend } from "vee-validate";
import { email, required, min, max, confirmed, numeric } from "vee-validate/dist/rules";

extend("email", {
  ...email,
  message: "Podaj poprawny adres e-mail"
});
extend("password", {
  params: ["target"],
  validate(value, { target }) {
    return value === target;
  },
  message: "Hasa nie s identyczne"
});
extend("required", {
  ...required,
  message: "To pole jest wymagane"
});
extend("numeric", {
  ...numeric,
  message: "To pole moe zawiera wycznie cyfry"
});
extend("max", {
  ...max,
  params: ["length"],
  message: "Zbyt duga nazwa. To pole moe mie maksymalnie {length} znakw"
});
extend("max_value", {
  params: ["specified"],
  validate(value, { specified }) {
    return value <= specified;
  },
  message: "Maksimum {specified}"
});
extend("min_value", {
  params: ["specified"],
  validate(value, { specified }) {
    return value >= specified;
  },
  message: "Minimum {specified}"
});
extend("min", {
  ...min,
  params: ["length"],
  message: "Zbyt krtkie. To pole musi mie minimum {length} znakw"
});
extend("confirmed", {
  ...confirmed,
  params: ["target"],
  message: "Hasa rni si od siebie"
});
